  border: 0;
  letter-spacing: 1px;
  transition: all .5s ease;
}

.forms-calldecor .get-buttons:focus, .forms-calldecor .get-buttons:hover, .forms-calldecor .get-buttons:active {
  background-color: #eb8d19;
}

.forms-calldecor .button-call {
  float: none;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
}

.forms-calldecor .mfp-close {
  position: absolute;
  right: 15px;
  font-size: 30px;
  top: 15px;
  border-radius: 3px;
  transition: all .75s ease;
}

.forms-calldecor .mfp-close:hover {
  color: red;
  transform: rotate(45deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(sizingMethod='auto expand', M11=0.7071067811865476, M12=-0.7071067811865475, M21=0.7071067811865475, M22=0.7071067811865476);
}

.forms-calldecor .mfp-close-btn-in .mfp-close {
  color: #fff;
  font-size: 22.5em;
  font-weight: bold;
  border-radius: 3px;
}

.forms-calldecor .success {
  color: #fff;
  background-color: #7cc15a;
  position: absolute;
  z-index: 1;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  opacity: 0;
  transition: all .5s ease;
}

.forms-calldecor .success.active {
  top: 0;
  opacity: 1;
}

.forms-calldecor .success p {
  display: flex;
  color: #fff;
  font-size: 18px;
  margin: auto;
  text-align: center;
}

.my-mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: all .35s ease-out;
}

.my-mfp-zoom-in.mfp-bg.mfp-ready {
  opacity: .4;
}

.my-mfp-zoom-in.mfp-bg.mfp-removing {
  opacity: 0;
}

.my-mfp-zoom-in.mfp-wrap .mfp-content {
